.wrapper {
  display: flex;
  justify-content: space-around;
  box-sizing: border-box;
  border-radius: 0.8rem;
  width: 100%;
  margin: 1.5rem auto 0 auto;
}

.test {
  border-left: 2px solid rgba(172, 172, 172, 0.25);
  margin: 1rem 0 1rem 0;
}

.income {
  display: flex;
  flex-direction: column;
  padding: 1rem 0 1rem 0.7rem;
}

.income h2 {
  align-self: center;
  padding-bottom: 0.1rem;
}

.income p {
  font-size: 1.5rem;
  color: rgb(90, 219, 90);
}

.expense {
  display: flex;
  flex-direction: column;
  padding: 1rem 0.7rem 1rem 0;
}

.expense h2 {
  align-self: center;
  padding-bottom: 0.1rem;
}

.expense p {
  font-size: 1.5rem;
  color: rgb(255, 91, 91);
  margin-left: 0.2rem;
}
